import Convert from './convert'
import LS from '../helpers/localStorage'
import ProjectDataParser from './projectDataParser'

import LayerFormat from './project_editor/layerFormatter'

import getAverageColor from './getAverageColor'

import emptyProjectData from './emptyProject.json'

/** @param  {Editor} edit */
function init(edit){
  const {vue, project, schema: projSchema} = edit;
  const thatTimeInMins = Convert.to.time.minutes(Date.now()); //    
  // const defaultLang = vue.$store.getters.lang;

  Object.assign(project.mn, {
    t_c : thatTimeInMins,         //   
    t_u : thatTimeInMins,         //   
    v_t : vue.$store.getters.vn,  //    
    v_s : projSchema._v,          //   
    // l   : defaultLang,            //  -  
  });

  
  // project.l[defaultLang] = { //      -
  //   example: 'Hello, Polyx!',
  // }
  
  // edit.create_map();
  // edit.add_data('b', { n: 'violet',   c: '7000d9', is_p: true });
  // edit.add_data('b', { n: 'green', c: '2fc908', is_p: true });
  // edit.add_data('b', { n: 'cyan',  c: '07aaf5', is_p: true });
  // edit.add_data('e', {
  //   w: 0.8,
  //   h: 0.8,
  //   c: 'ff7700',
  //   j: 212,
  //   a: 300,
  //   v: 150,
  // })
}

const Schema = {
  PROJECT: 'project',
  MAP:     'project.m_map',
  BLOCK:   'project.d_block',
  RPSTGS:  'project.rules',
}

export default class Editor {

    ___portate(){
      const oldProj = Convert.copy(this.project);

      // => MANIPULATE
      oldProj.d.b.forEach(b => {
        if(b.i!=null){
          b.i = oldProj.i.b.findIndex(i=>i.__c === b.i)
        }
      });
      // => MANIPULATE
      
      console.log('[ Project Editor ]:');
      console.dir({before: this.project,after: oldProj,});
      return oldProj;
    }
    ___saveProj(key, prolData){
      LS.save(key, prolData, Convert.to.json);
    }


    constructor(vue, JSONSchemaHelper) {
      this.vue = vue;
      this.schema = JSONSchemaHelper.schemas.get(Schema.PROJECT);
      this.jsonsh = JSONSchemaHelper;
    }

    createNewProject(key){
      // this.setProject(key, this.jsonsh.create(Schema.PROJECT));
      this.setProject(key, Convert.copy(emptyProjectData));
      init(this)
      return this.project;
    }

    saveChanges(){
      this.project.mn.t_u = Convert.to.time.minutes(Date.now()); //    
      // this.setProject(this.project);
      if(this.key){
        LS.save(this.key, this.project, Convert.to.json);
      }
    }


    setProject(key, data){
      this.key = key;
      this.project = Convert.copy(data);
      this.parser = new ProjectDataParser(this.project);

      // NOTE: DEBUG
      window.$$$PROJECT = this.project
    }

    setAutoPreview(base64data){
      this.project.mn.pv_a = base64data;
    }


    schema_block(){ return this.jsonsh.get(Schema.BLOCK) }
    async upd_data(categ, ix, data){
      switch(categ){
        case 'b':
          if(!data.c){
            data.c = await getAverageColor(this.parser.imgByCAI('b', data.i))
          }
          break;
      }
      this.project.d[categ][ix] = data;
    }
    async add_data(categ, data){
      switch(categ){
        case 'b':
          if(!data.c){
            data.c = await getAverageColor(this.parser.imgByCAI('b', data.i))
          }
          break;
      }
      this.project.d[categ].push(data);
    }



    schema_map(){ return this.jsonsh.get(Schema.MAP) }
    create_map(){
      const map = this.jsonsh.create(Schema.MAP);
      map.m.t_c = map.m.t_u = Convert.to.time.minutes(Date.now()); //    
      map.m.c = '111111';
      
      this.add_map(map);
      return map;
    }
    // edit_mapUpd(ix, data){
    //   data.m.t_u = Convert.to.time.minutes(Date.now());
    //   this.project.m[ix] = data;
    // }
    upd_map(mapId, mapdata){
      const map = this.project.m[mapId];
      map.d.p = mapdata.data.blocksProps;
      map.m.t_u = Convert.to.time.minutes(Date.now());
      map.l = {
        // b: LayerFormat.stringify(layers.b),
        m: LayerFormat.stringify(mapdata.layers.m),
        s: Convert.to.json(mapdata.layers.s),
      };
    }
    add_map(data){
      this.project.m.push(data);
    }


    
    upd_anim(ix, data){
      this.project.n[ix] = data;
    }
    add_anim(data){
      this.project.n.push(data);
    }


    schema_rpstgs(){ return this.jsonsh.get(Schema.RPSTGS).items.properties.s }

}