<template>
  <b-container fluid class="m-0 p-0 w-100">
    <b-form-select v-model="sidc" class="mb-3">
      <b-form-select-option-group v-for="(methList, ctxName) in rulesSelect" :key="ctxName"
        :label="ctxName"
      >
        <b-form-select-option v-for="(sidc, methName) in methList" :key="methName" :value="sidc">
          {{methName}}
        </b-form-select-option>
      </b-form-select-option-group>
    </b-form-select>
    <b-row class="m-0 mb-3 p-2 bg-lt">
      <!-- {{ruleData}} -->
      <RuleRow :inactive="true" v-if="sidc" :parser="parser" :idc="sidc"/>
    </b-row>
    <b-row v-if="ruleData!=null && ruleData.methProps && ruleData" class="m-0 p-2 bg-lt">
      <Field v-for="(schema, name, ix) in ruleData.methProps" :key="name"
        :model="[args, ix]"
        :schema="{...schema, title: name, enum: (schema._var ? vars : null)}"
        :val="args[ix]"
        :data="projdata"
        @change="isHasChanges = true"
        @validator="isInv => isHasInvalid = isInv"
      />
    </b-row>
    <b-row class="mt-4 w-100" align-h="center" cols="3">
      <b-button v-show="isRequiredEdit" @click.prevent="$emit('delete')" variant="danger"></b-button>
      <b-button class="ml-2" @click.prevent="commit" variant="primary" :disabled="isHasInvalid || !isHasChanges"></b-button>
    </b-row>
  </b-container>
</template>

<script>
import Field from '../../form/Field.vue'
import RuleRow from './RuleRow.vue'

export default {
  components: {
    Field,
    RuleRow,
  },
  props: ['projdata', 'parser', 'typeid', 'ctxid', 'idc', 'arguments', 'vars'],
  data() {
    return {
      sidc: this.idc, // selected id chain
      args: this.arguments || [],
      isHasInvalid: false,
      isHasChanges: false,
      isEditRules: false,
      ruleData: null,
    }
  },
  computed: {
    rulesSelect(){
      return this.parser.ruleList(this.typeid, this.ctxid)
    },
    isRequiredEdit(){
      return this.idc!=null;
    },
  },
  watch: {
    sidc(){ this.refreshRuleProps(); this.isHasChanges = true },
  },
  mounted(){
    if(this.sidc!=null){
      this.refreshRuleProps();
    }
  },
  methods: {
    refreshRuleProps(){
      this.ruleData = this.parser.ruleData(this.sidc);
      console.table({ruleData: this.ruleData, args: this.args});
    },
    commit(){
      
      const totalVal = [ this.sidc, this.args ];
      console.log('FormSchema result:');
      console.dir(totalVal);
      this.$emit('commit', totalVal)
    },
  },
}
</script>

