<template>
  <b-container v-if="isLoaded && !isEditMode" class="p-0 h-100 w-100" fluid>
    
    <b-container class="editor_col" style="width: 40%;">
      <img-pixel class="w-100" :src="projs.size !== 0 ? editor.parser.projPreview() : 'https://picsum.photos/600/300/'"/>
      <b-card v-if="projs.size !== 0"
        :title="editor.parser.projName('( )')"
        text-variant="light"
        class="brd-se"
        body-class="bg-pr"
        footer-class="bg-pr"
      >
        <!-- <template v-slot:header class="w-100 h-25">
        </template> -->
        <span class="text-secondary">{{projectBytes/1000}} </span>
        <b-card-text class="overflow-hidden" style="white-space: pre-line">
          {{ editor.parser.projDescr() }}
        </b-card-text>
        <template v-slot:footer>
          <b-button variant="danger" @click="deleteCurrentProj">
             
          </b-button>
          <b-button class="mr-2" @click="editCurrentProj" variant="success">
            
          </b-button>
        </template>
      </b-card>
      <b-card v-else class="text-center text-secondary">
        <h2> </h2>
      </b-card>
    </b-container>

    <b-container class="editor_col" style="width: 60%;">
      <b-button class="mr-2" variant="primary" @click="newProj">
        +   
      </b-button>
      <!-- <b-button  variant="primary" disabled>
        +  
      </b-button> -->
      <!-- <b-button  variant="success" @click="_test_portateProject">
        TEST:   
      </b-button> -->
      <!-- <b-button  variant="success" @click="_test_saveNewProjData">
        TEST:     
      </b-button> -->
      <ProjectList ref="projls" v-if="projs.size !== 0" :projects="projs" :selected="sprojkey" :editor="editor" @select="selectProject"/>
      <!-- TODO:     -->
    </b-container>


  </b-container>
  <ProjectEditorView v-else-if="isLoaded" :editor="editor" @exit="exitEdit"/>
</template>

<script>
import ProjectEditorView from '../components/editor/EditProject'
import ProjectList from '../components/editor/ProjectList'

import LS from '../helpers/localStorage'
import Convert from '../helpers/convert'
import Editor from '../helpers/projectEditor'
import JSONSchemaHelper from '../helpers/JSONSchema'
import getJSONBytes from '../helpers/getJSONSizeInBytes'

// import schemaProj from '../../public/schemas/project.json'

const LSK_projPef = 'p' // Local storage key: project prefix

function getFirstProjKey(projs) {
  return [...projs.keys()][0];
}

let _test_newProjData;

export default {
  data(){
    const projsLoaded = LS.many.loadAll(LSK_projPef, Convert.from.json);
    const projs = new Map();
    // Reverse list
    [...projsLoaded.keys()].reverse().forEach(k => projs.set(k, projsLoaded.get(k)))
    // Reverse list
    console.dir('projs:');
    console.dir(projs);

    return {
      isLoaded: false,
      editor: {},
      projectBytes: 0,
      projs, //  
      sprojkey: getFirstProjKey(projs), //  
      isEditMode: false,
    };
  },
  components: {
    ProjectEditorView,
    ProjectList,
  },
  watch: {
    isEditMode(v){
      this.$store.dispatch('setSidebarHide', v)
    },
  },
  async mounted(){
    const schemaProj = await import('../../public/schemas/project.json');
    const jsonsh = JSONSchemaHelper([schemaProj]);

    const projeditor = new Editor(this, jsonsh);
    this.editor = projeditor;

    this.selectProject(this.sprojkey);

    console.log('Project Schema:');
    console.table({
      source: schemaProj,
      inlined: this.editor.schema,
    });
    this.isLoaded = true;
  },
  methods: {
    _test_portateProject(){
      _test_newProjData = this.editor.___portate();
    },
    _test_saveNewProjData(){
      this.editor.___saveProj(this.editor.key, _test_newProjData);
    },

    editCurrentProj(){
      this.isEditMode = true;
      console.dir('\t :');
      console.dir([this.editor.key, this.editor.project]);
    },

    newProj(){
      const key = LS.many.getNextKey(LSK_projPef);
      const newProject = this.editor.createNewProject(key);
      
      this.projs.set(key, newProject);
      this.sprojkey = key;
      // LS.save(key, newProject, Convert.to.json);

      console.dir('\t :');
      console.dir(newProject);
      this.editCurrentProj();
    },
    deleteCurrentProj(){
      const vuec = this;
      this.$store.dispatch('dialog', {
        capt: '',
        msg: '    ?',
        btns: [[''], ['', 'danger']],
        onanswer(ix){
          if(ix === 1){
            vuec.projs.delete(vuec.editor.key);
            localStorage.removeItem(vuec.editor.key);
            vuec.$refs.projls.$forceUpdate();
          }
        }
      })
    },
    selectProject(projKey){
      this.sprojkey = projKey;
      const proj = {...this.projs.get(projKey)};
      this.editor.setProject(projKey, proj);
      this.projectBytes = getJSONBytes(JSON.stringify(proj)); // TODO:  , 

      console.log(`\t : ${projKey}`);
      console.dir(proj);
    },

    exitEdit(){
      this.isEditMode = false;
      console.log('key: '+this.editor.key)
      this.projs.set(this.editor.key, this.editor.project);
      // this.$refs.projls.$forceUpdate();
      // this.selectProject(this.editor.key);
      // const key = this.sprojkey; 
      // const loadedThisProj = LS.load(key, null, Convert.from.json);
      // this.projs.set(key, loadedThisProj);
      // this.selectProject(key);
    },
  },
}
</script>

<style>
  /* html>body div.card.card_projectinfo{
    height: 100%;
  } */
  /* .col > *{
    max-height: 100%;
  } */
  .editor_col{
    
    height: 98vh;
    margin-top: 1vh;
    float: left;
  }
</style>
