const N = {
  ANIMATION: '0',
  SPRITE:'1',
}

function generateSprite(elemid, scene, name, data, cfg){
  const el_imgContainer = document.createElement('div');
  el_imgContainer.style.display = 'none';
  document.getElementById(elemid).append(el_imgContainer);

  return new Promise(success=>{
    const el_img = new Image();
    el_img.src = data;
    el_imgContainer.appendChild(el_img);
    el_img.onload = function(){
      success(scene.textures.addSpriteSheet(name, el_img, cfg))
    };
  })
}

export default function(elemid, animData){
  console.log('[ Init anim preview ]')
  console.dir({animData});

  const Phaser = window.$libs.Phaser || {};
  const elSizePx = Math.trunc(
    +getComputedStyle(document.getElementById(elemid)).height.replace(/px/, '')
  )
  
  async function create(){
    await generateSprite(
      elemid,
      this,
      N.SPRITE,
      animData.sprite,
      { frameWidth: animData.fWidth, frameHeight: animData.fHeight },
    );
    this.anims.create({
      key: N.ANIMATION,
      frames: this.anims.generateFrameNumbers(N.SPRITE, { start: animData.fStart, end: animData.fEnd }),
      frameRate: animData.fRate,
      yoyo: animData.yoyo || false,
      repeat: -1
    });
    let sprite = this.add.sprite(0, 0, N.SPRITE).setOrigin(0);
    sprite.setDisplaySize(elSizePx, elSizePx);
    sprite.anims.play(N.ANIMATION);
  }

  const game = new Phaser.Game({
    parent: elemid,
    type: Phaser.CANVAS,
    width: elSizePx,
    height: elSizePx,
    pixelArt: true,
    // backgroundColor: '#00000000',
    scene: { create },
  });

  return {
    // setData(){},
    destroy(){
      game.destroy();
    }
  };
}