<template>
  <b-container class="text-light m-0 p-0" align-h="center" cols="3">
    <b-form-file
      class="m-0 p-0"
      v-model="file"
      :accept="schema._accept"
      :state="state"
      placeholder=" -"
      drop-placeholder="Drop file here..."
      browse-text=""
    />
    <b-row class="m-0 p-0">
      <AudioPlayer :src="asBase64" :vol="vol"/>
    </b-row>
  </b-container>
</template>

<script>
import getBase64 from '../../helpers/fileToBase64'
import AudioPlayer from './AudioPlayer'

export default {
  components: {
    AudioPlayer,
  },
  // filters: {},
  props: ['schema', 'value', 'model'],
  data() {
    return {
      file: null,
      asBase64: this.value || '',
      state: null,
      vol: window.$data.aud.vol,
    };
  },
  // computed: {},
  watch: {
    async file(v){
      this.asBase64 = await getBase64(v, '');
      if(this.schema && !(new RegExp(this.schema.pattern||'.').test(this.asBase64))){
        this.state = false;
      }else{
        this.state = null;
      }
      // console.log('Audio file:');
      // console.dir({v, data: this.asBase64, state: this.state, schema: this.schema});
      this.model(this.asBase64);
    },
  },
  // methods: {},
}
</script>

