<template>
  <div style="height: 100vh; width: 100vw;">

    <b-navbar class="t-b p-1 pl-2 pr-2 bg-pr">
      <b-button class="pt-0 pb-0 bg-lt border-0" @click="isEditFile ? canceFileEdit() : endProjectEdit()">
        {{isEditFile ? '' : ''}}
      </b-button>
      <b-nav class="ml-3 pt-0 pb-0">
        <b-nav-text class="p-0 text-pr">
          {{ editor.parser.projName('( )') }} 
        </b-nav-text>
        <b-nav-text v-if="isEditFile" class="p-0 pl-2 text-dr">
          {{ '/ ' + sFile.schema.title }}
        </b-nav-text>
      </b-nav>
      <b-navbar-nav class="ml-auto" v-show="!isEditFile">
        <b-button class="pt-0 pb-0 mr-3" @click="saveProject" :disabled="changedFiles.size == 0" :class="{'bg-lt text-pr brd-ac': changedFiles.size}"></b-button>
        <b-button class="pt-0 pb-0 bg-lt text-pr brd-ac" @click="runProject"></b-button>
      </b-navbar-nav>
    </b-navbar>

    <b-row v-if="!isEditFile" cols="5" class="m-0 p-3">
      <b-col class="p-1" v-for="(file,  ix) in files" :key="ix"> <!-- f[, , ] -->
        <b-card
          :class="`onhover_br-primary bg-pr text-center ${changedFiles.has(file[0]) ? 'brd-ac' : 'brd-se'}`"
          :footer-class="`text-nowrap p-1 text-center bg-lt ${changedFiles.has(file[0]) ? 'text-pr' : 'text-light'}`"
          :footer="file[1].title"
          @click="editFile(file[0], file[1], file[2])"
        >
          <!-- <img-pixel class="p-0" :src="`img/icons/e_${file[0]}.png`" fail="img/icons/e_file.png"/> -->
          <b-icon :class="'iconcol-'+file[0]" :icon="reskeyToIconAssociations[file[0]]" font-scale="12"/>
        </b-card>
      </b-col>
    </b-row>

    <div v-else class="w-100" style="height: 90%">
      <MapEditor v-if="sFile.schema.widget === 'mapeditor'"
        ref="editor"
        :schema="sFile.schema"
        :value="sFile.data"
        :editor="editor"
        @change="markChanged"
        @commit="commitFileChanges"
      />
      <!-- <LocalesEditor v-else-if="sFile.schema.widget === 'locres'"
        :project="editor.project"
        :schema="sFile.schema"
        :value="sFile.data"
        @change="markChanged"
        @commit="commitFileChanges"
      /> -->
      <AudioRes v-else-if="sFile.schema.widget === 'audres'"
        :editor="editor"
        :schema="sFile.schema"
        :data="sFile.data"
        @change="markChanged"
        @commit="commitFileChanges"
      />
      <AnimRes v-else-if="sFile.schema.widget === 'animres'"
        :editor="editor"
        :schema="sFile.schema"
        :data="sFile.data"
        @change="markChanged"
        @commit="commitFileChanges"
      />
      <DataRes v-else-if="sFile.schema.widget === 'datares'"
        :editor="editor"
        :schema="sFile.schema"
        :data="sFile.data"
        @change="markChanged"
        @commit="commitFileChanges"
      />
      <ImageResourses v-else-if="sFile.schema.widget === 'imgres'"
        :editor="editor"
        :schema="sFile.schema"
        :data="sFile.data"
        @change="markChanged"
        @commit="commitFileChanges"
      />
      <FormSchema v-else-if="sFile.schema.widget === 'form'"
        class="w-75 m-4"
        :editor="editor"
        :schema="sFile.schema"
        :value="sFile.data"
        @change="markChanged"
        @commit="commitFileChanges"
      /> 
      <b-container v-else-if="sFile.schema.widget === 'rulesres'" fluid class="h-100 text-light m-2 p-1">
        <RulesEditor 
          style="height: calc(100% - 8vh)"
          :editor="editor"
          :schema="sFile.schema"
          :value="sFile.data"
          @change="markChanged"
          @commit="commitFileChanges" 
        /> 
      </b-container>
        <!-- class="w-75 h-100 m-4" -->
      <b-container v-else class="t-b text-light text-center">
            
      </b-container>
    </div>

  </div>
</template>

<script>
import FormSchema from '../FormSchema'
import MapEditor from '../editor/EditMap'
// import LocalesEditor from '../editor/Locales'
import ImageResourses from '../editor/ImageResourses'
import DataRes from '../editor/DataRes'
import AnimRes from '../editor/AnimRes'
import AudioRes from '../editor/AudioRes'
import RulesEditor from './rules/RulesEditor'

window.addEventListener('keydown', e=>{
  if(e.key === 'Escape') vuec.endProjectEdit();
})
let vuec;

const reskeyToIconAssociations = {
  mn: 'file-text',
  d: 'columns-gap',
  i: 'images',
  m: 'map',
  a: 'music-note-beamed',
  n: 'film',
  // l: 'textarea-t',
  r: 'code-slash',
}

export default {
  data() {
    return {
      reskeyToIconAssociations,
      sFile: {
        name: '',
        schema: {},
        data: {},
      },
      changedFiles: new Set(),
      isEditFile: false,
    }
  },
  components: {
    FormSchema,
    MapEditor,
    // LocalesEditor,
    ImageResourses,
    DataRes,
    AnimRes,
    AudioRes,
    RulesEditor,
  },
  props: ['editor'],
  computed: {
    files(){
      const projData = this.editor.project;
      const res = [];
      const schemaProps = this.editor.schema.properties;
      for (let name in schemaProps) {
        if(name==='l')continue; // TODO:    
        const schema = schemaProps[name];
        res.push([name, schema, projData[name]])
      }
      // console.log(' [files]: ');
      // console.dir(res);
      return res;
    }
  },
  mounted(){
    vuec = this;
  },
  methods: {
    editFile(name, schema, data){
      // console.log('\tEditFile:');
      // console.table({ name, schema, data });
      Object.assign(this.sFile, { name, schema, data });
      this.isEditFile = true
    },
    markChanged(name = this.sFile.name){
      this.changedFiles.add(name);
    },
    canceFileEdit(){
      // console.log('\t : ');
      if(this.$refs.editor && this.$refs.editor.exit){
        this.isEditFile = !this.$refs.editor.exit();
      }else{
        this.isEditFile = false;
      }
    },
    commitFileChanges(newData){
      // console.log('\t : ');
      // console.dir(newData);
      this.editor.project[this.sFile.name] = newData;
      // console.log('\t: ');
      // console.dir(this.editor.project);
      this.isEditFile = false;
      this.markChanged();
    },
    endProjectEdit(){
      if(this.isEditFile){
        this.canceFileEdit();
      }else if(this.changedFiles.size){
        const vuec = this;
        this.$store.dispatch('dialog', {
          capt: '',
          msg: '  ,       ?',
          btns: [[''], [' ', 'danger'], ['C', 'primary']],
          onanswer(ix){
            switch (ix) {
              case 0: return;
              case 1: vuec.changedFiles.clear(); break;
              case 2: vuec.saveProject(); break;
            }
            vuec.$emit('exit');
          }
        });
      }else{
        this.$emit('exit');
      }
    },
    saveProject(){
      this.editor.saveChanges();
      this.changedFiles.clear();
      this.$forceUpdate();
    },
    runProject(){
      this.saveProject();
      const routeData = this.$router.resolve({
        path: '/game',
        query: {
          runloc: this.editor.key,
        },
      });
      window.open(routeData.href, '_blank');
    }
  }
}
</script>

<style>
  #link_gotoroot{
    color: var(--primary);
  }
  #link_gotoroot:hover{
    text-decoration: underline;
  }

  .iconcol-mn{  color: #cc5405}
  .iconcol-d{   color: #004ac2} 
  .iconcol-i{   color: #02b402}
  .iconcol-m{   color: #ad0202}
  .iconcol-a{   color: #6f00b4}
  .iconcol-n{   color: #a2b402}
  .iconcol-r{   color: #0490bb}
</style>