<template>
  <b-container fluid class="p-2">
    <b-row class="m-0 mb-3" align-h="between">

      <b-form-select v-if="!isEditMode"
        v-model="sCategName"
        class="w-25"
      >
        <b-form-select-option v-for="categ in categoriesNames" :key="categ" :value="categ">
          {{ category[categ].title }}
        </b-form-select-option>
      </b-form-select>
      <b-button-group v-else>
        <b-button class="mr-1" @click="isEditMode = false" >
          
        </b-button>
      </b-button-group>

      <b-button-group v-show="!isEditMode" >
        <b-button v-show="sFileId !== null" @click="editFile" variant="success">
          
        </b-button>
        <b-button v-show="sFileId !== null" class="ml-1" @click="delFile()" variant="danger">
          
        </b-button>
        <b-button class="ml-4" @click="addNewFile" variant="primary">
          
        </b-button>
      </b-button-group>

    </b-row>
    <b-list-group v-if="!isEditMode && sCategName" style="height: 85vh" class="p-3 overflow-auto">

      <div v-if="!data[sCategName].length"
        class="text-center text-secondary t-b pt-2"
      >
         
      </div>
      <b-list-group-item v-else v-for="(file, id) in data[sCategName]" :key="id"
        style="height: 10vh"
        class="mt-1 p-1 bg-pr brd-se text-light"
        :class="{'border-primary': sFileId === id}"
        @click="sFileId = id"
        @dblclick="editFile"
      >
        <b-row class="h-100">
          <b-col class="h-100 text-secondary t-b" cols=1>
            #{{id}}
          </b-col>
          <b-col class="h-100" cols=1>
            <!-- <img-pixel
              :src="editor.parser.imgByCAI(sCategName, file.i)"
              :style="'height:8vh; width:8vh; border:solid #' + file.c"
            /> -->
            <!-- <div :style="'width:5vh; height:5vh; background-color: #'"></div> -->
          </b-col>
          <b-col class="h-100 t-b">
            {{file.n}}
          </b-col>
        </b-row>
      </b-list-group-item>
      
    </b-list-group>
    <FormSchema v-else-if="sCategName"
      :editor="editor"
      :schema="schema.properties[sCategName].items"
      :value="isTargetEdit ? data[sCategName][sFileId] : {}"
      @commit="onFormSchemaCommit"
    />
  </b-container>
</template>

<script>
import FormSchema from '../FormSchema'

export default {
  components: {
    FormSchema,
  },
  props: ['editor', 'schema', 'data'],
  data() {
    return {
      isTargetEdit : false,
      sCategName   : '',
      sFileId      : null,
      isEditMode   : false,
    };
  },
  computed: {
    category(){
      return this.schema.properties;
    },
    categoriesNames(){
      return Object.keys(this.schema.properties);
    },
  },
  mounted(){
    this.sCategName = this.categoriesNames[0];
    console.log('sCategName schema:');
    console.dir(this.schema.properties[this.sCategName]);
  },
  methods: {
    editFile(){
      this.isTargetEdit = true;
      this.isEditMode = true;
    },
    addNewFile(){
      this.isTargetEdit = false;
      this.isEditMode = true;
    },
    delFile(id = this.sFileId){
      const categ = this.sCategName;
      this.data[categ].splice(id, 1); //   
      this.$emit('change');
      this.$forceUpdate();
    },
    onFormSchemaCommit(data){
      this.isEditMode = false;
      // const newSearchName = this.editFileName;
      const categ = this.sCategName;
      if(this.isTargetEdit){
        this.data[categ][this.sFileId] = data;
      }else{
        this.data[categ].push(data);
      }
      this.$emit('change')
    },
    // async onFormSchemaCommit(data){
    //   this.isEditMode = false;
    //   const categ = this.sCategName;
    //   if(this.isTargetEdit){
    //     await this.editor.upd_data(categ, this.sFileId, data);
    //   }else{
    //     await this.editor.add_data(categ, data);
    //   }
    //   this.$emit('change')
    //   this.$forceUpdate();
    // },
  },
}
</script>

