/*
  "obj.obj2:key?key=val"
  1)  obj.obj2  - ,     
  2)? :key      -    ,       
  2)? ?key=val  -       

*/

export default function(inObject, link){ // "obj.obj2:key#" => ["obj.obj2", "key#"] => [["obj", "obj2"], "key#"] (,  )
  let [stepsChain, finder] = link.split(':');
  const steps = stepsChain.split('.');
  let res = inObject;
  for (let i = 0; i < steps.length; i++) {
    res = res[steps[i]];
    if(res === undefined){
      return res;
    }
  }
  if(finder){
    const list = typeof res === 'object' ? Object.keys(res).map(k=>res[k]) : ( Array.isArray(res) ? res : null);
    const result = [];
    const [targetKey, condition] = finder.split('?')
    const [cKey, cVal] = (condition || '').split('=');
    list.forEach(v=>{
      if(v[targetKey]){
        if(condition){
          if(v[cKey] == cVal){
            result.push(v[targetKey])
          }
        }else{
          result.push(v[targetKey])
        }
      }
    })
    return result;
  }else{
    return res;
  }
}