<template>
  <b-container>
    <!-- <b-row> -->
      <!-- <b-button>X</b-button> -->
    <!-- </b-row> -->
    <b-row>
      <b-col class="p-0">
        <b-form-input v-model="newField"/>
      </b-col>
      <b-col class="p-0">
        <b-button class="w-100" :disabled="newField === '' || data.has(newField)" @click="set(newField)" variant="success"></b-button>
      </b-col>
      <b-col class="p-0">
        <b-button class="w-100" :disabled="!isChanged" @click="commit" variant="primary"></b-button>
      </b-col>
    </b-row>
    <b-row v-for="pair in data.entries()" :key="pair[0]">
      <b-col class="p-0">
        <b-form-input :value="pair[0]" disabled class="t-b"/>
      </b-col>
      <b-col class="p-0 text-light">
        <b-form-input :value="pair[1]" @input="v=>set(pair[0],v)"/>
      </b-col>
      <!-- <b-col class="p-0"> -->
      <b-button class="t-b" @click="del(pair[0])">X</b-button>
      <!-- </b-col> -->
    </b-row>
  </b-container>
</template>

<script>
// import cmp from './component'

export default {
  // components: {},
  // filters: {},
  props: ['value'],
  data() {
    return {
      data: new Map(),
      isChanged: false,
      newField: '',
    };
  },
  // computed: {},
  watch: {
    value(v){
      this.data.clear();
      if(v != null){
        for (const key in v) this.data.set(key, v[key]);
      }
      this.isChanged = false;
    },
  },
  methods: {
    set(k, v = ''){
      this.data.set(k, v);
      this.isChanged = true;
      this.$forceUpdate();
    },
    del(k){
      this.data.delete(k);
      this.isChanged = true;
      this.$forceUpdate();
    },
    commit(){
      this.isChanged = false;
      const obj = {};
      [...this.data.entries()].forEach(pair=>{
        obj[pair[0]] = pair[1];
      })
      console.log('obj:');
      console.dir(obj);
      this.$emit('commit', obj)
    },
  },
}
</script>