<template>
  <div :id="id"></div>
</template>

<script>
import initPreview from './initPreview'
import loadScript from '../../../helpers/scriptLoader'

let idai = 0;

export default {
  props: ['value'],
  data() {
    return {
      int: {
        destroy: ()=>0,
      },
    };
  },
  computed: {
    id: () => `apv_${idai++}`,
  },
  async mounted(){
    await loadScript(`e/libs/phaser_3.23.0.js`, true);
    this.int = initPreview(this.id, this.value);
  },
  destroyed(){
    this.int.destroy();
  },
}
</script>