<template>
  <div class="mx-auto bg-pr brd-se text-light p-2" style="border:2px solid; max-height: 80vh; overflow-x: hidden; overflow-y: auto">
    <div class="m-0 p-0 h-75">
      <Field v-for="(p, k) in val" :key="k"
        :model="[val, k]"
        :schema="schema.properties[k]"
        :val="p"
        :isreq="requiredFields.has(k)"
        :data="data"
        @change="isHasChanges = true"
        @validator="isInv => isHasInvalid = isInv"
      />
      <b-row class="mt-4" align-h="center" cols="3">
        <!-- <b-button @click.prevent="cancel" class="mr-2"></b-button> -->
        <b-button @click.prevent="commit" variant="primary" :disabled="isHasInvalid || !isHasChanges"></b-button>
      </b-row>
    </div>
  </div>
</template>

<script>
import Field from './form/Field'
import Convert from '../helpers/convert'

export default {
  data(){
    return {
      val: {},
      rls: this.value && this.value.rls ? Convert.copy(this.value.rls) : [],
      isHasInvalid: false,
      isHasChanges: false,
    }
  },
  props: ['editor', 'schema', 'value', 'data'],
  components: {
    Field,
  },
  created(){
    const privateFields = new Set(this.schema._private);
    for (let key in this.schema.properties) {
      if(privateFields.has(key))continue;
      // NOTE:     'list'
      if(this.schema.properties[key].widget === 'list')continue;
      // NOTE:     'list'
      if(this.value && this.value[key] !== undefined){
        this.val[key] = this.value[key];
      }else{
        this.val[key] = null;
      }
    }
    // console.log(`\t     :`);
    console.log('{ FormSchemaSimple }');
    console.dir({
      value: this.value,
      schema: this.schema,
      val: this.val,
    });
  },
  computed: {
    requiredFields(){
      return new Set(this.schema.required);
    },
  },
  methods: {
    // cancel(){
    //   this.$emit('cancel')
    // },
    commit(){
      if(this.$refs.rlsparser){
        this.$refs.rlsparser.$destroy();
      }
      const totalVal = {
        ...this.value,
        ...this.val,
      }
      this.$emit('commit', totalVal)
      // console.log('FormSchema result:');
      // console.dir(totalVal);
      // const isValid = JSONSchema.validate(this.schema, this.val);
      // if(!isValid){
      //   console.log(` `);
      //   console.dir(JSONSchema.errors);
      //   return  
      // }
    },
  },
}
</script>
