<template>
  <b-row fluid class="h-100 m-0 p-0">
    <b-col class="h-100 p-1 bg-dr brd-se" style="border: 2px solid" cols="3">
      <b-list-group>
        <b-list-group-item v-for="(rulesPack, ix) in val" :key="ix"
          class="mt-1 p-0 pl-2 bg-pr"
          :class="sRPId===ix ? 'brd-ac' : 'brd-se'"
          @click="selectRulesPack(ix)"
          @dblclick="editName(ix)"
        >
          <span v-if="sEditRPId !== ix">
            <span class="text-secondary" >{{ix}}</span> {{rulesPack.n}}
          </span>
          <b-input class="m-0 bg-transparent text-light" v-model="eName" @keydown.enter="setName(ix)" @keydown.esc="sEditRPId=null" v-else/>
        </b-list-group-item>
      </b-list-group>
      <b-button class="w-100 mt-1 bg-lt brd-se" size="sm" @click="addNewRulesPack">+   </b-button>
    </b-col>
    <b-col v-if="sRulesPack" class="h-100 " cols="9">

      <b-row class="m-1 w-100 bg-lt" align-v="center">
        <b-icon icon="gear" class="w-25 rulepack_stgs" font-scale="3" @click="toggleStgsEditor()"/>
        <b-card
          header=""
          class="brd-se w-75"
          header-class="pl-2 p-0 bg-lt brd-se"
          body-class="p-0 bg-dr rl_body-evn"
        >
          <RuleRow 
            v-if="sRulesPack.event"
            :parser="ruleparser"
            :idc="sRulesPack.event[0]"
            :args="sRulesPack.event[1]"
            @click="editRule(false)"
          />
          <b-button v-else variant="success" size="sm" class="m-1 p-0 pl-3 pr-3" @click="addNewRule(false)"> </b-button>
        </b-card>
      </b-row>

      <b-card
        header=""
        class="m-1 w-100 brd-se"
        header-class="pl-2 p-0 bg-lt brd-se"
        body-class="p-0 bg-dr rl_body-vars"
      >
        <VarRow v-for="(v, ix) in sRulesPack.vars" :key="ix"
          :data="v"
          @delete="deleteVar(ix)"
        />
        <b-form-input class="w-25 m-1 bg-pr brd-se text-light" v-model="newVarName" @keydown.enter="addVar(newVarName)"/>
      </b-card>

      <b-card
        header=""
        class="m-1 w-100 brd-se"
        header-class="pl-2 p-0 bg-lt brd-se"
        body-class="p-0 bg-dr rl_body-acts"
      >
        <RuleRow v-for="(rulePair, ix) in sRulesPack.actions" :key="ix"
          :parser="ruleparser"
          :idc="rulePair[0]"
          :args="rulePair[1]"
          @click="editRule(true, ix)"
        />
        <b-button variant="success" size="sm" class="m-1 p-0 pl-3 pr-3" @click="addNewRule(true)"> </b-button>
      </b-card>

    </b-col>
    <b-modal
      id="m_ruleedit"
      centered
      hide-footer
      :title="e_capt"
      size="xl"
      header-class="bg-lt text-light ff-main"
      body-class="bg-pr text-light ff-sec"
      footer-class="bg-dark text-light"
    >
      <RuleEdit
        style="height: 70vh"
        :projdata="editor.project"
        :parser="ruleparser"
        :typeid="e_tid"
        :ctxid="e_cid"
        :idc="e_idc"
        :arguments="args"
        :vars="vars"
        @delete="deleteRule"
        @commit="closeRuleEditor"
      />
    </b-modal>
    <b-modal
      id="m_ruleedit_stgs" 
      centered
      hide-footer
      title=""
      size="xl"
      header-class="bg-lt text-light ff-main"
      body-class="bg-pr text-light ff-sec"
      footer-class="bg-dark text-light"
      no-close-on-backdrop
      no-close-on-esc
    >
      <FormSchemaSimple v-if="sRPId && val[sRPId]"
        :editor="editor"
        :schema="editor.schema_rpstgs()"
        :value="val[sRPId].s || {}"
        :data="editor.project"
        @commit="commitStgsChanges"
      /> 
        <!-- class="w-75 m-4" -->
        <!-- @change="markChanged" -->
      <!-- <b-button variant="success" class="mx-auto mt-4 w-50" @click="toggleStgsEditor(true)"></b-button> -->
      
        <!-- @validator="isInv => isHasInvalid = isInv" -->
        <!-- @change="isHasChanges = true" -->
    </b-modal>
  </b-row>
</template>

<script>
import RuleRow from './RuleRow.vue'
import RuleEdit from './RuleEdit.vue'
import FormSchemaSimple from '../../FormSchemaSimple.vue'
import VarRow from './VarRow'

import SchemasRules from '../../../../public/schemas/rules.json'
import RulesParser from '../../../helpers/rulesParser'
// import Convert from '../helpers/convert'

const defaultRulePackName = '( Nameless rules pack )';

export default {
  components: {
    RuleRow,
    RuleEdit,
    VarRow,
    FormSchemaSimple,
  },
  props: ['editor', 'schema', 'model', 'value'],
  data() {
    return {
      e_tid: 0,
      e_cid: 0,
      e_idc: '',
      e_capt: '',
      e_isact: false,
      e_ix: 0,
      args: [],
      // val: Convert.copy(this.value) || [],
      val: this.value,
      sRPId:null,
      eName: '',
      newVarName: '',
      sEditRPId:null,
      isTargetEdit: false,
      sRulesPack: null,
      vars: null,
    };
  },
  computed: {
    ruleparser(){
      return new RulesParser(SchemasRules);
    },
  },
  // watch: {},
  mounted(){
    if(this.value.length !== 0){
      this.selectRulesPack(0);
    }
    // console.log('RulesEditor:');
    // console.table({
    //   '0.1.0': this.ruleparser.ruleData('0.1.0'),
    //   '0.2.0': this.ruleparser.ruleData('0.2.0'),
    //   '0.2.1': this.ruleparser.ruleData('0.2.1'),
    //   '0.2.2': this.ruleparser.ruleData('0.2.2'),
    //   '0.2.3': this.ruleparser.ruleData('0.2.3'),
    //   '0.2.4': this.ruleparser.ruleData('0.2.4'),
    // });
  },
  destroyed(){
    this.$emit('commit', this.value);
    this.$parent.$forceUpdate();
  },
  methods: {
    selectRulesPack(ix = this.sRPId){
      const rp = this.val[ix];

      // FIXME: ,      RulePair-
      this.val[ix].s = {...this.val[ix].s}
      // if(rp){
      //   console.log('replaceVariablesByIndexes:');
      //   console.table({
      //     a_from: rp,
      //     b_to: this.ruleparser.replaceVariablesByIndexes(rp),
      // // const rp = RulesParser.compressVariableNames(rulespack);
      //   });
      // }
      this.sRPId = ix;
      this.sRulesPack = this.ruleparser.rulesPack(rp);
      this.refreshVars();
      // console.table(this.val.map(rp => rp.s));
    },
    refreshVars(){
      const rulePack = this.val[this.sRPId];
      this.vars = this.ruleparser.rulesPacksVars(rulePack);
      console.log('[refreshVars] vars:');
      console.table({vars: this.vars});
    },
    addNewRulesPack(){
      const newRPId = this.val.push(this.ruleparser.newRulesPack(defaultRulePackName));
      this.selectRulesPack(newRPId-1);
      this.$emit('change');
    },
    trySetNameToCurrentRulePack(){
      const evt = this.val[this.sRPId].e;
      if(this.val[this.sRPId].n === defaultRulePackName && evt){
        const rd = this.ruleparser.ruleData(evt[0]);
        this.val[this.sRPId].n = `${rd.ctxName}: ${rd.methName}`;
      }
    },
    editName(ix){
      this.eName = this.val[ix].n;
      this.sEditRPId = ix;
    },
    setName(ix){
      this.val[ix].n = this.eName;
      this.sEditRPId = null;
      this.$emit('change');
    },

    editRule(isAction, ix){
      this.isTargetEdit = true;
      this.e_isact = isAction;
      this.e_ix = ix;
      const curRulePair = isAction ? this.val[this.sRPId].a[ix] : this.val[this.sRPId].e;
      this.openRuleEditor(curRulePair[0], curRulePair[1]);
    },
    addNewRule(isAction){
      this.isTargetEdit = false;
      this.e_isact = isAction;
      this.openRuleEditor();
    },

    openRuleEditor(idc, args = []){
      this.e_capt = idc ? ' ' : ' ';
      this.e_tid = this.e_isact ? 1 : 0;
      this.e_cid = this.e_isact ? null : this.schema._ctx; //     
      this.e_idc = idc;
      this.args = args;
      this.$bvModal.show('m_ruleedit');
    },
    toggleStgsEditor(isClose = false){
      if (isClose) {
        this.$bvModal.hide('m_ruleedit_stgs');
        this.$forceUpdate();
      } else {
        this.$bvModal.show('m_ruleedit_stgs');
      }
    },
    commitStgsChanges(data){
      this.val[this.sRPId].s = {...data};
      this.$emit('change');
      // console.log('[ commitStgsChanges ] data:');
      // console.dir({
      //   toRP: this.val[this.sRPId], 
      //   new: data,
      // });
      this.toggleStgsEditor(true);
    },
    deleteRule(){
      this.$bvModal.hide('m_ruleedit');
      if (this.e_isact) {
        this.val[this.sRPId].a.splice(this.e_ix, 1);
      }else{
        this.val[this.sRPId].e = null;
      }
      this.selectRulesPack();
      this.refreshVars();
      this.$emit('change');
    },
    closeRuleEditor(data){
      this.$bvModal.hide('m_ruleedit');
      if (this.e_isact) {
        const targetRuleList = this.val[this.sRPId].a;
        if(this.isTargetEdit){
          targetRuleList.splice(this.e_ix, 1, data);
        }else{
          targetRuleList.push(data);
        }
      }else{
        this.val[this.sRPId].e = data;
        this.trySetNameToCurrentRulePack();
      }
      this.selectRulesPack();
      this.refreshVars();
      this.$emit('change');
      this.$forceUpdate();
    },

    addVar(name){
      this.val[this.sRPId].v.push(['', name]);
      this.refreshVars();
      this.$emit('change');
    },
    deleteVar(ix){
      this.val[this.sRPId].v.splice(ix, 1);
      this.refreshVars();
      this.$emit('change');
    },
  },
}
</script>

<style>
  .rl_body-evn{
    height: 5vh;
  }
  .rl_body-vars{
    height: 16vh;
    max-height: 16vh;
    overflow-y: auto;
  }
  .rl_body-acts{
    height: 52vh;
    max-height: 52vh;
    overflow-y: auto;
  }

  .rulepack_stgs:hover{
    color: var(--t-primary);
  }
</style>