<template>
  <b-row class="w-100 m-0 bg-dark p-2">
    <audio ref="audio_player" :src="src" :autoplay="isAutoPlay" :loop="isRepeat" @timeupdate="timeupdate"></audio>
    <b-row class="w-100 m-0">
      <b-button size="sm" class="mr-1" variant="success" @click="play">Play</b-button>
      <b-button size="sm" class="mr-1" variant="primary" @click="pause">Pause</b-button>
      <b-button size="sm" class="mr-3" variant="danger" @click="stop">Stop</b-button>

      <b-button size="sm" class="mr-1" @click="isRepeat = !isRepeat" :class="{'bg-dark' : isRepeat}">Repeat</b-button>
      <b-button size="sm" class="mr-3" @click="isAutoPlay = !isAutoPlay" :class="{'bg-dark' : isAutoPlay}">AutoPlay</b-button>
      
      <b-form-input class="h-100 w-25" type="range" max="10" v-model="volume" @mousewheel="rangeWhell"></b-form-input>
      {{src ? (getFileSize(src)/1000)+' Kb' : ''}}
    </b-row>
    <div ref="audio_player_progress" style="height: 2vh" class="w-100" @click="setProgress">
      <b-progress class="h-100 w-100 mt-1 bg-secondary">
        <b-progress-bar :value="cur*100" :max="max*100" variant="danger"/>
      </b-progress>
    </div>
  </b-row>
</template>

<script>
import getFileSize from '../../helpers/getJSONSizeInBytes'

const steps = 10;
let el_progbar;

export default {
  // components: {},
  // filters: {},
  props: ['src', 'vol'],
  data() {
    return {
      cur: 0,
      max: 0,
      volume: 0,
      isRepeat: false,
      isAutoPlay: true,
    };
  },
  // computed: {},
  watch: {
    volume(v){
      el_progbar.volume = v/steps;
    }
  },
  mounted(){
    this.volume = this.vol * steps;
    el_progbar = this.$refs.audio_player;
  },
  methods: {
    getFileSize,
    play(){
      el_progbar.play();
    },
    pause(){
      el_progbar.pause();
    },
    stop(){
      this.pause();
      el_progbar.currentTime = 0;
    },
    timeupdate(){
      this.cur = el_progbar.currentTime;
      this.max = el_progbar.duration;
    },
    rangeWhell(e){
      const delY = e.deltaY
      this.volume -= delY>0 ? 1 : -1;
    },
    setProgress(e){
      const tarEl = this.$refs.audio_player_progress;
      const maxWidth = +getComputedStyle(tarEl).width.replace(/px/,'');
      const progress = e.offsetX / maxWidth;
      el_progbar.currentTime = this.cur = this.max * progress;
    }
  },
}
</script>

