function copy(obj) {
  return JSON.parse(JSON.stringify(obj))  
}

function keyByIxFromObject(obj, ix) {
  return Object.keys(obj)[ix];
}
function valueByIxFromObject(obj, ix) {
  return obj[Object.keys(obj)[ix]];
}

function getListCaptToIdc(methList, idcPrefix) {
  const list = {};
  Object.keys(methList).forEach((methKey, methid)=>{
    list[methKey] = idcPrefix + methid;
  })
  return list;
}

const argPrefix = '#';
const varPrefix = '@';

export default class RulesParser {

  constructor(schema) {
    this.schema = schema;
    // this.data = data;
  }

  newRulesPack(name){
    return {
      n: name,
      e: null,
      v: [],
      a: [],
      s: {},
    }
  }

  rulesPack(rp){
    return {
      name: rp.n,
      event: rp.e,
      vars: rp.v,
      actions: rp.a,
      settings: rp.s || {},
    }
  }
  rulesPacksVars(rp){
    const evArgs = rp.e ? this.ruleData(rp.e[0]).args : null;
    return [
      ...(evArgs ? evArgs : []).map(v=>argPrefix + v),
      ...rp.v.map(v=>varPrefix + v[1])
    ];
  }
  rulesPacksVarsMap(rp){
    const evArgs = rp.e ? this.ruleData(rp.e[0]).args : null;
    return new Map(
      (evArgs ? evArgs : []).map((v, ix)=>[argPrefix + v, ix])
      .concat(rp.v ? rp.v.map(v=>[varPrefix + v[1], v[0]]) : []) 
    );
  }
  createGetVarValFunction(varMap, args){
    return function(key){
      if(typeof key !== 'string')return key;
      const val = varMap.get(key);
      if(key.startsWith(argPrefix)){
        // console.log('createGetVarValFunction:');
        // console.table({varMap, args, argPrefix});
        return args[val];
      }else if(key.startsWith(varPrefix)){
        return val
      }else{
        return key
      }
    };
  }
  compressVariableNames(rulepack) {
    const vars = this.rulesPacksVars(rulepack);
    const aliasToIx = {};
    const rp = copy(rulepack);
    vars.forEach((val, ix) => aliasToIx[val] = val.match(/^./)+ix);
    rp.a.forEach(rpair=>{
      rpair[1] = rpair[1].map(v=>aliasToIx[v] || v)
    });
    // console.log('compressVariableNames:');
    // console.table({aliasToIx, bef:rulepack, z_aft:rp});
    return rp;
  }
  // replaceVariablesByIndexes(rulepack) {
  //   const vars = this.rulesPacksVars(rulepack);
  //   const aliasToIx = {};
  //   const rp = copy(rulepack);
  //   vars.forEach((val, ix) => aliasToIx[val] = ix)
  //   rp.a.forEach(rpair=>{
  //     rpair[1] = rpair[1].map(v=>{
  //       if(typeof v === 'string' && (v.startsWith(varPrefix) ||v.startsWith(argPrefix)))
  //        ? aliasToIx[v] : v
  //     })
  //   });
  //   return rp;
  // }

  ruleData(idchain){
    const [typeid, ctxid, methid] = idchain.split('.').map(v=>+v);
    const rulesCtxs = valueByIxFromObject(this.schema, typeid);
    const ctxMeths = valueByIxFromObject(rulesCtxs, ctxid);
    const methodProps = copy(valueByIxFromObject(ctxMeths, methid));
    const args = methodProps.$;
    delete methodProps.$;

    return {
      tid: typeid,
      cid: ctxid,
      mid: methid,
      isEvent: typeid===0,
      isAction: typeid===1,
      typeName: keyByIxFromObject(this.schema, typeid),
      ctxName: keyByIxFromObject(rulesCtxs, ctxid),
      methName: keyByIxFromObject(ctxMeths, methid),
      methProps: Object.keys(methodProps).length !== 0 ? methodProps : null,
      args,
    }
  }

  ruleList(typeid, targCtxId){
    const listOfType = valueByIxFromObject(this.schema, typeid);
    const res = {};
    Object.keys(listOfType).forEach((ctxKey, ctxid)=>{
      if(targCtxId!=null && targCtxId !== ctxid)return;
      res[ctxKey] = getListCaptToIdc(listOfType[ctxKey], `${typeid}.${ctxid}.`);
    })
    return res;
  }
}