<template>
  <div class="c_rulerow m-0 pl-2 pr-2 ff-sec" :class="{inact: inactive}" @click="$emit('click')">
    <b :style="'color:#'+colorList[ruleData.cid]">{{ruleData.ctxName}}:</b>
    {{ruleData.methName}}
    <span v-show="ruleData.methProps" :style="'color:#ccc'">
      (
        <span class="c_param" v-for="(schema, name, ix) in ruleData.methProps" :key="ix">
          {{ix===0?'':')('}} {{name}}
          <!-- <span class="rr_type" :style="'color:#aaa'">
            : <span>{{getType(schema)}}</span>
          </span> -->
          <span v-if="args">
            = <span class="text-pr">{{getValue(ix)}}</span>
          </span>
        </span>
      )
    </span>
    <span v-show="ruleData.args" class="text-se">
      => [
        <span class="c_param" v-for="(name, ix) in ruleData.args" :key="ix">
          {{ix===0?'':', '}} 
          <span class="text-pr">{{name}} </span>
        </span>
      ]
    </span>
  </div>
</template>

<script>
const colorList = ['9a74fb', '00CC00', 'f63e3e', '8888FF', 'CCFF33', 'f6ad36', '26ec89'];

export default {
  props: ['inactive', 'parser', 'idc', 'args'],
  data() {
    return {
      colorList,
    };
  },
  computed: {
    ruleData(){
      return this.parser.ruleData(this.idc);
    },
  },
  mounted(){
    // console.log('[ inactive ]:');
    // console.table({inactive:this.inactive});
  },
  methods: {
    getType(schema){
      let res = schema.type;
      if(schema.type === 'array'){
          const min = schema.minItems, max = schema.maxItems;
          res = `${schema.items.type}[${min===max ? min : min+'-'+max}]`;
      }
      return res;
    },
    getValue(ix){
      return ''+this.args[ix];
    },
  },
}
</script>

<style>
  .c_rulerow:nth-child(odd){
    background-color: #00000008;
  }
  .c_rulerow:nth-child(even){
    background-color: #ffffff08;
  }
  .c_rulerow:not(.inact):hover{
    background-color: #fff2;
    cursor: pointer;
  }
  .c_rulerow:not(.inact):not(:hover) .rr_type{
    display: none;
  }
  /* .erh{
    color: #9a74fb;
    color: #00CC00;
    color: #f63e3e;
    color: #8888FF;
    color: #CCFF33;
    color: #f6ad36;
    color: #26ec89;
  } */
</style>