<template>
  <b-container fluid class="m-0 p-0 h-100 w-100 t-b">
    
    <b-row class="m-1" v-if="isEditMode">

      <b-button-group class="mr-2 w-25">
        
        <!-- <b-form-select v-if="true"> 
          <b-form-select-option v-for="(map, id) in editor.parser.maps" :key="id" :value="id">
           #{{id}} {{ map.m.n || '( )' }}
          </b-form-select-option>
        </b-form-select> -->

        <!-- <b-button></b-button> -->
      </b-button-group>

      <b-dd
        menu-class="b_dd-menu brd-se bg-dr p-1"
        toggle-class="bg-pr p-1 m-0 brd-se"
        no-caret
      >
        <template v-slot:button-content :class="{'brd-ac': sEditObjectType === 0 }">
          <img-pixel style="float:left" :src="blocks[sBlockId].i" class="b_blockcolor brd-se" :color="blocks[sBlockId].c"/>
          <img-pixel style="float:left" :src="blocks[sBlockIdAlt].i" class="ml-1 b_blockcolor-mini brd-ac" :color="blocks[sBlockIdAlt].c"/>
        </template>
        <b-row class="m-1">
          <div v-for="(b, ix) in blocks" :key="ix"
            class="b_selblock mr-1 mb-1"
            :class="{'border-warning': sBlockId === ix, 'border-primary': sBlockIdAlt === ix}"
            @click="selectBlock(ix)"
            @contextmenu="selectBlock(ix, true)"
          >
            <img-pixel :src="b.i" :color="b.c"/>
          </div>
        </b-row>
        <b-row class="m-0 p-2 bg-pr brd-se text-light" style="border: 2px solid">
          <div class="b_blockcolor border-0 mr-2" :style="'background: #' + blocks[sBlockId].c"></div>
          <span class="t-b">{{blocks[sBlockId].n}}</span>
        </b-row>
      </b-dd>

      <b-dd
        v-if="structs.length>1"
        menu-class="b_dd-menu bg-dark p-0"
        toggle-class="p-1 m-0"
        no-caret
      >
        <template v-slot:button-content :class="{'border-primary': sEditObjectType === 1 }">
          <img-pixel style="float:left" :src="structs[sStructId].i" class="b_blockcolor border-warning" :color="structs[sStructId].c"/>
          <img-pixel style="float:left" :src="structs[sStructIdAlt].i" class="ml-1 b_blockcolor-mini border-primary" :color="structs[sStructIdAlt].c"/>
        </template>
        <b-row class="m-1">
          <div v-for="(b, ix) in structs" :key="ix"
            class="b_selblock mr-1 mb-1"
            :class="{'border-warning': sStructId === ix, 'border-primary': sStructIdAlt === ix}"
            @click="selectStruct(ix)"
            @contextmenu="selectStruct(ix, true)"
          >
            <img-pixel :src="b.i" :color="b.c"/>
          </div>
        </b-row>
        <b-row class="m-0 p-2 bg-secondary text-light">
          <div class="b_blockcolor border-0 mr-2" :style="'background: #' + blocks[sStructId].c"></div>
          <span class="t-b">{{blocks[sStructId].n}}</span>
        </b-row>
      </b-dd>



      <!-- <b-button class="ml-1" @click="test">test</b-button> -->
      

    </b-row>

    <b-row v-if="!isEditMode" class="p-2 text-light">
      <b-col cols=4>
        <!-- <b-row> -->
          <b-card v-for="(map, id) in maps" :key="id"
            :class="{'brd-ac': sMapId === id}"
            class="bg-pr"
            @dblclick="selectMap(id)"
          >
            #{{id}} {{ map.m.n || '( )' }}
          </b-card>
        <!-- </b-row> -->
      </b-col>
      <b-col cols=8>
        <FormSchema
          :editor="editor"
          :schema="editor.schema_map().properties.m"
          :value="maps[sMapId].m"
          @commit="commitMapManifestChanges"
        />
      </b-col>
    </b-row>

    <b-row v-show="isEditMode" class="m-0 p-0 w-100">
      <b-col cols="3" class="p-2">
        <PropsList ref="propslist" :value="blockPropsData" @commit="blockPropsCB"/>
      </b-col>
      <b-col cols="9" class="p-0">
        <div class="h-100 w-100 brd-se" id="screen_editor"></div>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import FormSchema from '../FormSchema'
// import EditProject from '../../helpers/editProject';
import DataShare from '../../helpers/dataShare';
import PropsList from './maps/PropsList';

export default {
  data() {
    return {
      isEditMode: false,
      isReady: false,
      maps: this.editor.parser.maps,
      sMapId: 0,
      layers: [],
      sLayerName: '',
      sEditObjectType: 0,
      sBlockId: 1,
      sBlockIdAlt: 0,
      sStructId: 1,
      sStructIdAlt: 0,
      blockPropsData: null,
      blockPropsCB: ()=>0,
    };
  },
  props: ['editor'],
  components: {
    FormSchema,
    PropsList,
  },
  watch: {
    sLayerName(sLayerKey){ DataShare.setData({sLayerKey}) },
  },
  mounted(){
    const vuec = this;
    DataShare.clearTriggersAndEvents();

    window.$events.onEngineLoad = ()=>vuec.isReady = true;
    window.$triggers.setBlockPropsEdit = function(data, cb) {
      vuec.blockPropsData = data;
      vuec.blockPropsCB = cb;
      vuec.$refs.propslist.$forceUpdate();
    }

    this.$store.dispatch('loadEngine', 'editor')
      .then(()=>{ console.log(`  `) })
      .catch(console.error)
  },
  destroyed(){
    window.$triggers.destroy();
  },
  computed: {
    blocks(){
      const blocks = this.editor.parser.sumBlockDataList();
      return [
        {i:'img/icons/delete_block.png', n: ' ', c:'0000'},
        ...blocks,
      ];
    },
    structs(){
      const structs = this.editor.parser.sumStructDataList();
      return [
        {i:'img/icons/delete_block.png', n: ' ', c:'0000'},
        ...structs,
      ];
    }
  },
  methods: {
    selectMap(id){
      if(!this.isReady)return;
      window.$triggers.destroy();
      this.isEditMode = true;
      this.sLayerName = 'm';
      this.sMapId = id
      this.layers = this.maps[id].l;
      // console.log('this.layers:');
      // console.dir(this.layers);

      // DataShare.setData({layers: this.layers})

      const startData = {
        parent: 'screen_editor',
        isLocal: true,
        project: this.editor.project,
        mapId: id,
      };
      window.$triggers.start(startData)
      // return id;
    },
    setEditObjectType(id = 0){
      this.sEditObjectType = id;
      window.$data.editObjectType = id;
    },
    selectBlock(ix, isAlt){
      this.setEditObjectType();
      if(!isAlt){
        this.sBlockId = ix;
        window.$data.sBlockId = ix-1;
      }else{
        this.sBlockIdAlt = ix;
        window.$data.sBlockIdAlt = ix-1;
      }
    },
    selectStruct(ix, isAlt){
      this.setEditObjectType(1);
      if(!isAlt){
        this.sStructId = ix;
        window.$data.sStructId = ix-1;
      }else{
        this.sStructIdAlt = ix;
        window.$data.sStructIdAlt = ix-1;
      }
    },
    test(){
      console.log('window.$data:');
      console.table(window.$data);
    },
    saveLayerData(){
      if(window.$triggers.getMapData){
        const editData = window.$triggers.getMapData();
        if(editData){
          this.$emit('change');
          this.editor.upd_map(this.sMapId, editData)
        }
      }
    },
    commitMapManifestChanges(data){
      this.maps[this.sMapId].m = data;
      this.$emit('change');
    },

    exit(){ //  ,  true,   ""  
      if(this.isEditMode){
        this.saveLayerData();
        return this.isEditMode = false;
      }
      // this.editor.edit_mapUpd(0, data);
      // this.selectMap(this.sMapId);
      return true;
    }
  },
}
</script>

<style>
  #screen_editor>*{
    border: solid 2px;
    margin:0 !important;
  }

  .b_dd-menu{
    width: 60vh;
  }
  .b_blockcolor{
    height: 4vh;
    width: 4vh;
    border: solid;
  }
  .b_blockcolor-mini{
    height: 3vh;
    width: 3vh;
    border: solid;
  }
  .b_selblock{
    height: 6vh;
    width: 6vh;
    margin: 0;
    padding: 0;

    font-size: 2vh;
    line-height: 6vh;
    text-align: center;

    /* background-color: var(--secondary); */
    color: #0000 var(--light);
    user-select: none;
    border: solid;
    box-sizing: border-box;
  }
  .b_selblock:hover{
    background-color: #5a6268;
  }
  /* .b_selblock.active{
    border-color: var(--primary);
  } */
</style>