<template>
  <b-list-group style="height: 89vh" class="pr-2 text-nowrap overflow-auto">
    <b-list-group-item
      v-for="key_proj in projects.entries()" :key="key_proj[0]"
      class="mt-2 text-light d-flex p-0 bg-pr"
      :class="selected === key_proj[0] ? 'brd-ac' : 'brd-se'"
      @click="$emit('select', key_proj[0])"
    >
      <img-pixel class="w-25" :src="editor.parser.projPreview(key_proj[1])"/>
      <div class="pl-3">
        <h2 class="text-nowrap overflow-hidden">{{ editor.parser.projName('( )', key_proj[1]) }}</h2>
        : {{ editor.parser.projTimeUpd(key_proj[1]) }}
      </div>
    </b-list-group-item>
  </b-list-group>
</template>

<script>
export default {
  props: ['projects', 'selected', 'editor'],
  watch: {
    projects(v){
      console.log(':');
      console.dir(v)
    },
  },
}
</script>

