<template>
    <b-form-input
      :type="itype"
      :placeholder="schema.example"
      :min="schema.minimum"
      :max="schema.maximum"
      :minLength="schema.minLength"
      :maxLength="schema.maxLength"
      :value="value"
      @change="normalize"
      @input="model"
      :state="state"
    />
      <!-- size="sm" -->
      <!-- v-model="model" -->
      <!-- :formatter="schema.pattern" -->
</template>

<script>
const typeAssociations = {
  string: 'text',
  number: 'number',
  integer: 'number',
  // integer: '',
}

export default {
  // data() {
  //   return {
  //   };
  // },
  props: ['schema', 'value', 'model', 'state'],
  // components: {}
  methods: {
    normalize(){
      let v;
      switch (this.itype) {
        case 'number':
          v = +this.$el.value.replace(/[^.\d-]/g, '');

          if(isNaN(v) || this.schema.minimum>this.$el.value){
            v = this.schema.minimum
          }else if(this.schema.maximum<this.$el.value){
            v = this.schema.maximum
          }
          if(this.schema.type==='integer'){
            v = Math.trunc(v);
          }
          // console.table([this.$el.value,v]);
          this.$el.value = v
          break;
      
        // default:
        //   break;
      }
    },
  },
  computed: {
    itype(){
      return typeAssociations[this.schema.type];
    }
  }
}
</script>

