<template>
  <b-row class="m-1 w-100">
    <b-col v-if="!isSpecial" class="text-nowrap text-right" cols="2">
      {{schema.title + ': '}}
      <span v-show="isreq" class="text-warning">*</span>
    </b-col>
    <b-col class="form_input-cnt" :class="{'w-100': isSpecial}">

      <SelectImage
        v-if="schema.widget === 'image'"
        :schema="schema"
        :model="val => v = val"
        :value="v"
      />

      <SelectAudio
        v-else-if="schema.widget === 'audio'"
        :schema="schema"
        :model="val => v = val"
        :value="v"
      />

      <!-- <List
        v-else-if="schema.widget === 'list'"
        :schema="schema"
        :model="val => v = val"
        :value="v"
      /> -->

      <b-form-select v-else-if="selectList.length" v-model="v" size="sm">
        <b-form-select-option v-for="(val, ix) in selectList" :key="ix"
          :value="isTarIndex ? ix : val"
          :state="isValid"
        >
          {{val}}
        </b-form-select-option>
      </b-form-select>
      
      <b-form-select v-else-if="schema.enum" v-model="v" size="sm">
        <b-form-select-option v-for="val in schema.enum" :key="val"
          :value="val"
          :state="isValid"
        >
          {{val}}
        </b-form-select-option>
      </b-form-select>

      <b-form-textarea v-else-if="schema.widget === 'textarea'"
        size="sm"
        :placeholder="schema.example"
        v-model="v"
        :rows="v != null? Math.min(Math.max((v.match(/\n/g) || []).length+1, 4), 12) : 4" 
        :state="isValid"
      />
        <!-- :rows="..." -       4-12 -->
      <b-form-checkbox v-else-if="schema.type === 'boolean'"
        size="lg"
        v-model="v"
        :state="isValid"
      />

      <FInput v-else
        :schema="schema"
        :model="val => v = val"
        :value="v"
        :state="isValid"
      />
        <!-- :model="v" -->
    </b-col>
  </b-row>
</template>

<script>
import SelectAudio from './Audio'
import SelectImage from './Image'
// import List from './List'
import FInput from './Input'
import getDataByLink from '../../helpers/getObjectFieldByLink'


export default {
  data() {
    return {
      isValid: null,
      isTarIndex: this.schema._tarIx, //    
      isSpecial: new Set(['image', 'audio']).has(this.schema.widget),
      v: this.val,
    };
  },
  props: ['schema', 'model', 'val', 'isreq', 'data'],
  components: {
    FInput,
    SelectAudio,
    SelectImage,
    // List,
  },
  computed: {
    selectList(){//  
      if(this.schema._link){
        const link = this.schema._link;
        return link ? getDataByLink(this.data, link) : [];
      }else if(this.schema._list){
        if(this.schema._isNegat){
          return [
            ...this.schema._list,
            ...this.schema._list.map(c=>'Not '+c)
          ];
        }else{
          return this.schema._list;
        }
      }
      return [];
    },
  },
  mounted(){
    console.table(this.selectList)
  },
  watch: {
    v(v){
      this.$emit('change');
      let val = typeof v === 'string' ? v.trim() : v;
      if(this.schema.type === 'number' && !this.schema._var){
        val = val.length !== 0 ? +val : null;
      }
      val = ('' + val).length === 0 ? null : val;
      console.dir({val});
      this.model[0][this.model[1]] = val; //   
      if((this.isreq===true || this.isreq==null) && val === ''){
        this.isValid = false;
      }else if(this.schema.pattern && val !== '' && !new RegExp(this.schema.pattern).test(val)){
        // console.log(this.schema.pattern);
        this.isValid = false;
      }else {
        this.isValid = null;
      }
      this.$emit('validator', this.isValid === false)
    }
  }
}
</script>

<style>
  .form_input-cnt>*{
    height: 100%;
    /* font-size: 120%; */
    /* font-weight: bold; */
    /* border-color: var(--secondary); */
    /* background-color: #eee; */
  }
  .is-invalid{
    border-color: darkorange !important;
  }
</style>