<template>
  <b-container class="text-light p-3" align-h="center" cols="3">
    <b-form-file
      v-model="file"
      :accept="schema._accept"
      :state="state"
      placeholder="   PNG"
      drop-placeholder="Drop file here..."
    ></b-form-file>
    <b-row class="mt-4">
      <img-pixel
        class="w-100"
        style="height: 50vh; background-position: center top !important;"
        :src="asBase64"
      />
      
      <!-- <b-col class="t-b">
        : {{ file && file.size || '*'}} bite<br>
        : {{ file && file.type || '*'}}
      </b-col> -->
    </b-row>
  </b-container>
</template>

<script>
import getBase64 from '../../helpers/fileToBase64';

export default {
  // components: {},
  // filters: {},
  props: ['schema', 'value', 'model'],
  data() {
    return {
      file: null,
      asBase64: this.value || '',
      state: null,
    };
  },
  // computed: {},
  watch: {
    async file(v){
      this.asBase64 = await getBase64(v, '');
      if(this.schema && !(new RegExp(this.schema.pattern||'.').test(this.asBase64))){
        this.state = false;
      }else{
        this.state = null;
      }
      // console.log('Img file:');
      // console.dir({v, data: this.asBase64, state: this.state, schema: this.schema});
      this.model(this.asBase64);
    },
  },
}
</script>

