import Ajv from 'ajv'

const typeGen = {
  'array': ()=>[],
  'object': ()=>({}),
  'string': ()=>'',
  'number': ()=>0,
  'boolean': ()=>!1,
}

function compile(schemaRoot) {
  const definitions = schemaRoot.definitions;
  // const additions = schemaRoot._additions;

  function recursive(defs, schema) {
    if(schema.$ref && schema.$ref.startsWith('#')){
      const def = {...defs[schema.$ref.slice(1)]};
      delete def.$id;
      schema = {
        ...def,
        ...schema,
      }
      delete  schema.$ref;
      // TODO:   ,    
    }
    for (let k in schema) {
      if(typeof schema[k] !== 'object')continue;
      schema[k] = recursive(defs, schema[k])
      // Object.assign(schema[k], additions[k]||{})
    }
    return schema;
  }

  return recursive(definitions, schemaRoot);
  // debugger
}

function create(schema) {
  const r = {}
  const props = schema.properties;
  if(!props)return typeGen['object']();
  for (let k in props) {
    if(props[k].type !== 'object'){
      if(props[k].default == null){
        const t = props[k].type;
        if(!t)return null; 
        r[k] = typeGen[t]();
      }else{
        r[k] = props[k].default;
      }
    }else{
      r[k] = create(props[k])
    }
  }
  return r;
}

export default function(schemasList) {
  const originalSchemas = new Map(schemasList.map(s=>[s.$id, s]));
  const inlinedSchemas = new Map();
  originalSchemas.forEach((schema, schId)=>{
    const compiledSchema = compile({...schema});
    const defs = compiledSchema.definitions;
    inlinedSchemas.set(schId, compiledSchema);

    if(defs){
      for (const defId in defs) {
        const defSchema = defs[defId];
        
        inlinedSchemas.set(schId+'.'+defId, defSchema);
      }
    }
  })

  const ajv = new Ajv({
    schemas: inlinedSchemas.values(),
  });

  console.log('[ JSONSchemaHelper ] schemas:');
  console.table({
    originalSchemas,
    inlinedSchemas,
  });
  return {
    ajv,
    /**       @param {Object|string} schema_or_sid - JSON-      id*/
    create(schema_or_sid){
      let schema = schema_or_sid;
      if(typeof schema_or_sid === 'string'){
        schema = inlinedSchemas.get(schema_or_sid);
        if(!schema){
          console.error(`[ JSONSchemaHelper ]    "${schema_or_sid}"`)
        }
      }
      return create(schema);
    },
    get(k){
      return inlinedSchemas.get(k)
    },
    validate(schema, data){
      return ajv.validate(schema, data)
    },
    schemas: inlinedSchemas,
  };
}
